(function(){
    require('./arrayShuffle'); 
    function getRandomArrayElements(arr, e, count) {
        var shuffled = arr.slice(0), i = arr.length, min = i - count, temp, index;
        function c(no){
            let index = Math.floor((i + 1) * Math.random());
            let temp = shuffled[index];
            if(no === temp){
                console.log('重复')
                debugger
                c(e);
            }else{
                console.log(temp)
                return temp;
            }
        }
        outPoint:
        while (i-- > min) {
            index = Math.floor((i + 1) * Math.random());
            temp = shuffled[index];
            if(temp === e){
                break outPoint;
            }
            shuffled[index] = shuffled[i];
            shuffled[i] = temp;
        }
        return shuffled.slice(min);
    }
    let hiraWord = ['あ','い','う','え','お','か','き','く','け','こ','さ','し','す','せ','そ','た','ち','つ','て','と','な','に','ぬ','ね','の','は','ひ','ふ','へ','ほ','ま','み','む','め','も','や','ゆ','よ','ら','り','る','れ','ろ','わ','を','ん'];
    let audioArr = ["a.mp3","i.mp3","u.mp3","e.mp3","o.mp3","ka.mp3","ki.mp3","ku.mp3","ke.mp3","ko.mp3","sa.mp3","si.mp3","su.mp3","se.mp3","so.mp3","ta.mp3","ti.mp3","tu.mp3","te.mp3","to.mp3","na.mp3","ni.mp3","nu.mp3","ne.mp3","no.mp3","ha.mp3","hi.mp3","hu.mp3","he.mp3","ho.mp3","ma.mp3","mi.mp3","mu.mp3","me.mp3","mo.mp3","ya.mp3","yu.mp3","yo.mp3","ra.mp3","ri.mp3","ru.mp3","re.mp3","ro.mp3","wa.mp3","wo.mp3","n.mp3"];
    let kataWord = ["ア","イ","ウ","エ","オ","カ","キ","ク","ケ","コ","サ","シ","ス","セ","ソ","タ","チ","ツ","テ","ト","ナ","ニ","ヌ","ネ","ノ","ハ","ヒ","フ","ヘ","ホ","マ","ミ","ム","メ","モ","ヤ","ユ","ヨ","ラ","リ","ル","レ","ロ","ワ","ヲ","ン"];
    let pronunciation = new Vue({
        el: '#pronunciation-page',
        data: {
            hiraWord: Array.from(hiraWord),
            kataWord: Array.from(kataWord),
            audioArr:Array.from(audioArr),
            fraction: 0,
            hira: false,
            kata: false,
            random: 0,
            pronunciation: '',
            randomHira: [],
            randomKata: [],
        },
        methods: {
            reset: function(){
                console.log('reset')
                this.random = ~~(Math.random()*this.audioArr.length);
                this.getAudio();
                this.randomHiraWord();
                this.randomKataWord();
                this.hira = false;
                this.kata = false;
                $('.card').removeClass('mdui-color-red-a400 no-drop mdui-color-green');
            },
            refresh: function(){
                this.hiraWord = Array.from(hiraWord);
                this.kataWord = Array.from(kataWord);
                this.reset();
                this.fraction = 0;
            },
            verification: function(e, event, type){
                console.log('verification')
                let index = (type == 'hira' ? this.hiraWord : this.kataWord).indexOf(e)
                if(index == this.random){
                    $(event.currentTarget).parents('.bb').find('.card').addClass('no-drop');
                    $(event.currentTarget).parents('.bb').find('.card.mdui-color-red-a400').removeClass('mdui-color-red-a400');
                    $(event.currentTarget).addClass('no-drop mdui-color-green');
                    if(type == 'hira'){
                        this.hiraWord.splice(index, 1);
                        this.hira = true;
                        this.fraction++;
                    }else if(type == 'kata'){
                        this.kataWord.splice(index, 1);
                        this.kata = true;
                        this.fraction++;
                    }
                }else{
                    $(event.currentTarget).addClass('no-drop mdui-color-red-a400');
                    this.fraction--;
                }

                if(this.hira && this.kata){
                    setTimeout(function(){ 
                        pronunciation.reset();
                        pronunciation.audioArr.splice(index, 1);
                     }, 500);
                }
            },
            playAudio: function(e){
                console.log('playAudio')
                $('#audio')[0].play();
            },
            getAudio: function(){
                console.log('getAudio')
                let src = './audio/'+this.audioArr[this.random];
                this.pronunciation = this.audioArr[this.random].replace(/\.mp3/, '');
                return src;
            },
            randomHiraWord: function(){
                console.log('randomHiraWord')
                let item = this.hiraWord[this.random];
                let arr = getRandomArrayElements(this.hiraWord, item, 3);
                arr.splice(~~(Math.random() * 4), 0, item);
                this.randomHira = arr
                return arr;
            },
            randomKataWord: function(){
                console.log(this.kataWord[this.random])
                console.log('randomKataWord')
                let item = this.kataWord[this.random];
                let arr = getRandomArrayElements(this.kataWord, item, 3);
                arr.splice(~~(Math.random() * 4), 0, item);
                this.randomKata = arr
                return arr;
            }
        },
        mounted: function(){
            this.random = ~~(Math.random()*this.audioArr.length);
            this.randomKataWord();
            this.randomHiraWord();
        }
    })
}())